@import '_colors.scss';
@import '_fonts.scss';
html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
}

#home {
    background-color: $charcoal;
    width: 100vw;
    height: 100vh;
    nav {
        position: fixed;
        left: 0;
        top: 0;
        width: 100vw;
        z-index: 99;
        .navbar-content {
            padding: .5rem;
            margin-right: 3rem;
            margin-left: 1.5rem;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        .navbar-image {
            position: absolute;
            z-index: -1;
        }
        .logo {
            display: inline-block;
        }
        .nav-links {
            font-size: 2rem;
            list-style: none;
            display: flex;
        }
        .nav-item a {
            display: inline-block;
            padding: 0 6rem;
            text-decoration: none;
            color: white;
            font-family: "Avant-Garde-Std-Extra-Light"
        }
        .nav-item:hover {
            background-color: white;
        }
        .nav-item:hover a {
            color: darkgreen;
        }
        .logo img {
            vertical-align: middle;
        }
    }
    .parallaxe {
        position: absolute;
        left: 0;
        top: 0;
        width: 100vw;
        height: 100vh;
        clip: rect(0px, 100vw, 100vh, 0px);
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -ms-flex-align: center;
        -webkit-align-items: center;
        -webkit-box-align: center;
        align-items: center;
        overflow: hidden;
        .plane {
            position: absolute;
            .top {
                top: 0;
                left: 0;
                width: 100vw;
                height: auto;
                z-index: 10;
                top: -2.5%;
            }
            .background {
                top: 10%;
                left: 0;
                width: 120%;
                height: auto;
                z-index: 1;
            }
        }
        .layer {
            top: 0;
            min-width: 100vw;
            height: auto;
            opacity: 1;
        }
        .l-10 {
            z-index: 2;
        }
        .l-9 {
            z-index: 1;
            /* @TODO : Delete the !important when parallax.js will allow to setup a right start position  */
            left: -40px !important;
        }
        .l-8 {
            z-index: 2;
            /* @TODO : Delete the !important when parallax.js will allow to setup a right start position  */
            left: 40px !important;
        }
        .l-7 {
            z-index: 3;
        }
        .l-6 {
            z-index: 4;
        }
        .l-5 {
            z-index: 5;
        }
        .l-4 {
            z-index: 6;
            /* @TODO : Delete the !important when parallax.js will allow to setup a right start position  */
            left: -40px !important;
        }
        .l-3 {
            z-index: 7;
        }
        .l-2 {
            z-index: 8;
            /* @TODO : Delete the !important when parallax.js will allow to setup a right start position  */
            left: -40px !important;
        }
        .l-1 {
            z-index: 9;
            /* @TODO : Delete the !important when parallax.js will allow to setup a right start position  */
            left: -40px !important;
        }
        .slogan {
            position: absolute;
            text-align: center;
            left: calc(50% - (387px / 2)) !important;
            top: 70vh !important;
            z-index: 20;
            font-family: "Avant-Garde-Std-Extra-Light";
            font-size: 1rem;
            color: white;
        }
        .discover-btn {
            position: absolute;
            text-align: center;
            left: calc(50% - (305px / 2)) !important;
            top: 80vh !important;
            z-index: 20;
            padding: .6rem 6rem;
            font-family: "Avant-Garde-Std-Extra-Light";
            font-size: 1.3rem;
            background-color: $blush;
            color: white;
            border-radius: .5rem;
            &:hover {
                cursor: pointer;
            }
        }
    }
}